import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { drawGame } from "../../../../_actions/player.actions";
import { AppActions, AppState } from "../../../../_types";
import { IMelodition } from "../../../../_types/player.types";
import BackCanvas from "./BackCanvas";
import FrontCanvas from "./FrontCanvas";
import SvgLine from "./SvgLine";

interface Props {
  Melo: IMelodition;
  drawGame: (canvas: any) => void;
}

const PianoCanvas: React.FC<Props> = ({ Melo, drawGame }) => {
  return (
    <div
      style={{
        width: Melo.canvasSize?.width,
        height: Melo.canvasSize?.height,
        position: "relative",
        boxShadow: "inset 0 1px 4px 0px black",
      }}
    >
      <FrontCanvas Melo={Melo} drawGame={drawGame} />
      <BackCanvas Melo={Melo} />
      {Melo.canvasSize && <SvgLine {...Melo.canvasSize} />}
    </div>
  );
};
const mapStateToProps = (state: AppState) => ({
  Melo: state.melodition,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  drawGame: bindActionCreators(drawGame, dispatch),
});

const connectedPianoCanvas = connect(
  mapStateToProps,
  mapDispatchToProps
)(PianoCanvas);

export default connectedPianoCanvas;
