import { useCallback, useRef } from "react";
import { LoginResponse } from "../_types/user.types";
import fallImg from "../assets/img/melodition.jpg";

export const useCallbackRef = (rawCallback: any) => {
  const cleanupRef = useRef(null);
  const callback = useCallback(
    (node) => {
      if (cleanupRef.current) {
        // cleanupRef.current();
        cleanupRef.current = null;
      }
      if (node) {
        cleanupRef.current = rawCallback(node);
      }
    },
    [rawCallback]
  );

  return callback;
};

export interface PathFinder {
  post?: number;
  guide?: number;
  course?: number;
  lesson?: number;
  exercise?: number;
  length: number;
}
export const pathFinder = (path: string): PathFinder => {
  const pathSplit = path.split("/");
  let compiledPath: PathFinder = { length: 0 };
  if (pathSplit.length === 3) {
    compiledPath = {
      [pathSplit[1]]: Number(pathSplit[2]),
      length: 1,
    };
  } else if (pathSplit.length === 4) {
    const typeSplit = pathSplit[3].split("=");
    compiledPath = {
      [pathSplit[1]]: Number(pathSplit[2]),
      [typeSplit[0]]: Number(typeSplit[1]),
      length: 2,
    };
  } else if (pathSplit.length === 5) {
    const type1Split = pathSplit[3].split("=");
    const type2Split = pathSplit[4].split("=");
    compiledPath = {
      [pathSplit[1]]: Number(pathSplit[2]),
      [type1Split[0]]: Number(type1Split[1]),
      [type2Split[0]]: Number(type2Split[1]),
      length: 3,
    };
  }
  return compiledPath;
};

export const getUserLocal = (key: string): LoginResponse => {
  const item = localStorage.getItem(key);
  return item !== null ? JSON.parse(item) : undefined;
};

export const getTheTime = (minutes: number = 0): number => {
  var d = new Date();
  var t = d.getTime();
  const ms = minutes * 60000;
  return t + ms;
};

export const UniqId = (): string => {
  return getTheTime(Math.random()).toString(36);
};

export const thumbnailsFinder = (
  img: string | { medium: string } | null
): string => {
  let image = fallImg;
  if (img) {
    if (typeof img === "object") image = img.medium;
    else if (typeof img === "string") image = img;
  }
  return image;
};
