import React, { useEffect } from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core";
import SvgKeys, { LastKey } from "../Finger/SvgKeys";
import {
  midiNoteOff,
  midiNoteOn,
  drawPiano,
} from "../../../../_actions/player.actions";
import { ICanvas } from "../../../../_types/player.types";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { AppActions, AppState } from "../../../../_types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "row-reverse",
      justifyContent: "center",
      flexWrap: "nowrap",
      margin: "0 auto",
      minWidth: "215px",
      width: "100%",
    },
    back: {
      margin: theme.spacing(0, 2),
    },
  })
);

type Props = ReturnType<typeof mapStateToProps> &
  ReturnType<typeof mapDispatchToProps>;

const Piano: React.FC<Props> = (props) => {
  const { theme, Melo, drawPiano } = props;

  let isDown = false;
  let playedKey: any = null;
  const emulatedKeys = {
    z: 48,
    s: 49,
    x: 50,
    d: 51,
    c: 52,
    v: 53,
    g: 54,
    b: 55,
    h: 56,
    n: 57,
    j: 58,
    m: 59,
    ",": 60,
    l: 61,
    ".": 62,
    ";": 63,
    "/": 64,
    q: 65,
    2: 66,
    w: 67,
    3: 68,
    e: 69,
    4: 70,
    r: 71,
    t: 72,
    6: 73,
    y: 74,
    7: 75,
    u: 76,
    i: 77,
    9: 78,
    o: 79,
    0: 80,
    p: 81,
    "-": 82,
    "[": 83,
    "]": 84,
  };
  let counter = 35;
  let firstkey = 36;
  const KeysArray = {} as SVGGElement[];

  const keyboardUp = (e: KeyboardEvent) => {
    const key = e.key as keyof typeof emulatedKeys;
    if (emulatedKeys.hasOwnProperty(key)) {
      const note = emulatedKeys[key];
      midiNoteOff(note);
      playedKey = null;
    }
  };
  const keyboardDown = (e: KeyboardEvent) => {
    const key = e.key as keyof typeof emulatedKeys;
    if (emulatedKeys.hasOwnProperty(key)) {
      if (key !== playedKey) {
        const note = emulatedKeys[key];
        midiNoteOn(note);
        playedKey = key;
      }
    }
  };

  const mouseUpReset = () => {
    isDown = false;
  };

  useEffect(() => {
    document.addEventListener("keydown", keyboardDown);
    document.addEventListener("keyup", keyboardUp);
    document.addEventListener("mouseup", mouseUpReset);
    return () => {
      document.removeEventListener("keydown", keyboardDown);
      document.removeEventListener("keyup", keyboardUp);
    };
  });

  const classes = useStyles();

  const mouseDown = (e: React.MouseEvent<SVGGElement, MouseEvent>) => {
    e.preventDefault();
    const ref: SVGGElement = e.currentTarget;
    const note = ref.tabIndex;
    isDown = true;
    playedKey = note;
    midiNoteOn(note);
  };
  const mouseOver = (e: React.MouseEvent<SVGGElement, MouseEvent>) => {
    e.preventDefault();
    const ref: SVGGElement = e.currentTarget;
    const note = ref.tabIndex;
    if (isDown && note !== playedKey) {
      playedKey = note;
      midiNoteOn(note);
    }
  };
  const mouseLeave = (e: React.MouseEvent<SVGGElement, MouseEvent>) => {
    e.preventDefault();
    const ref: SVGGElement = e.currentTarget;
    const note = ref.tabIndex;
    if (isDown) {
      midiNoteOff(note);
      playedKey = null;
    }
  };
  const mouseUp = (e: React.MouseEvent<SVGGElement, MouseEvent>) => {
    e.preventDefault();
    const ref: SVGGElement = e.currentTarget;
    const note = ref.tabIndex;
    isDown = false;
    midiNoteOff(note);
    playedKey = null;
  };
  const svgWidth = (i: number) => {
    const num = i === 1 ? 668 : 584;
    return num;
  };
  const counterFunction = (octSize: number) => {
    if (octSize > 6) {
      counter = 23;
      firstkey = 24;
    } else if (octSize > 3) {
      counter = 35;
      firstkey = 36;
    } else if (octSize > 1) {
      counter = 47;
      firstkey = 48;
    } else {
      counter = 59;
      firstkey = 60;
    }
  };
  const detectSize = (height: number) => {
    if (height > 700) {
      return height / 16;
    } else if (height > 500) {
      return height / 16;
    } else if (height > 300) {
      return height / 16;
    } else if (height > 100) {
      return height / 16;
    } else {
      return height / 16;
    }
  };
  const setKeyRef = (ref: SVGGElement | null) => {
    if (ref) {
      const key = ref.tabIndex;
      KeysArray[key] = ref;
    }
  };
  const setRef = (ref: HTMLDivElement) => {
    if (ref && Melo.pianoLoading) {
      if (Melo.playMode) {
        const pianoSize = ref.getBoundingClientRect();
        const width = pianoSize.width;
        const height = Math.floor(window.innerHeight - (pianoSize.height + 64));
        const tapSize = detectSize(height);
        const baseLine = tapSize * 4 + 8;
        const pianoLeft = pianoSize.left;
        const canvas = { width, height, baseLine, pianoLeft };
        pianoSizes(KeysArray, canvas, tapSize);
      } else {
        pianoSizes(KeysArray);
      }
    }
  };
  const pianoSizes = (
    keyRefs: SVGGElement[],
    canvasSize?: ICanvas,
    tapSize?: number
  ) => {
    drawPiano(keyRefs, canvasSize, tapSize);
  };
  const themer = theme === "dark" ? 1 : 0;
  counterFunction(Melo.octave);
  return (
    <div
      ref={setRef}
      className={classes.root}
      style={{ filter: `invert(${themer})` }}
    >
      {Array.from(Array(Melo.octave), (_, i) => "oct" + (i + 1)).map(
        (key, i) => (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            id={key}
            key={key}
            width={svgWidth(Melo.octave - i)}
            height="100%"
            viewBox={`0 0 ${svgWidth(Melo.octave - i)} 275`}
            fill="none"
            style={
              i !== Melo.octave - 1
                ? { marginRight: 8 / Melo.octave }
                : undefined
            }
          >
            {SvgKeys.map((k) => (
              <g
                key={key + "-" + k.note}
                id={k.id}
                tabIndex={++counter}
                onMouseDown={mouseDown}
                onMouseUp={mouseUp}
                onMouseLeave={mouseLeave}
                onMouseOver={mouseOver}
                className={counter === firstkey ? "first" : k.note}
                ref={setKeyRef}
              >
                <path
                  id="back"
                  d={k.front}
                  fill={k.id}
                  stroke={k.id === "white" ? "#B5B5B5" : undefined}
                />
                <path
                  id="front"
                  d={k.back}
                  fill={k.id === "white" ? "black" : "rgb(255 255 255 / 65%)"}
                />
                {counter === 60 ? (
                  <polygon points={"30,265 40,255 50,265"} fill={"black"} />
                ) : null}
              </g>
            ))}
            {Melo.octave - i === 1 ? (
              <g
                id="C"
                className="last"
                tabIndex={++counter}
                onMouseDown={mouseDown}
                onMouseUp={mouseUp}
                onMouseLeave={mouseLeave}
                onMouseOver={mouseOver}
                ref={setKeyRef}
              >
                <path
                  id="back"
                  d={LastKey.front}
                  fill="white"
                  stroke="#B5B5B5"
                />
                <path id="front" d={LastKey.back} fill="black" />
              </g>
            ) : null}
          </svg>
        )
      )}
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.auth.theme,
  Melo: state.melodition,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  drawPiano: bindActionCreators(drawPiano, dispatch),
});

const connectedPiano = connect(mapStateToProps, mapDispatchToProps)(Piano);

export default connectedPiano;
