import {
  PlayerActionTypes,
  REQUEST_PLAYER,
  RESET_PLAYER,
  ENTER_PLAYMODE,
  EXIT_PLAYMODE,
  START_PLAYER,
  STOP_PLAYER,
  DRAW_PIANO,
  IMelodition,
  DRAW_GAME,
  DRAW_FINGER,
  RESIZE_PLAYER
} from "../_types/player.types";

const defaultPlayer: IMelodition = {
  playMode: false,
  loading: true,
  onAir: false,
  fingerLoading: true,
  pianoLoading: true,
  gameLoading: true,
  octave: 5,
  tapSize: 32,
};

export const playerReducer = (
  state = defaultPlayer,
  action: PlayerActionTypes
): IMelodition => {
  switch (action.type) {
    case REQUEST_PLAYER:
      return { ...state, loading: false };
    case DRAW_PIANO:
      return { ...state, ...action.payload, pianoLoading: false };
    case DRAW_GAME:
      return { ...state, gameLoading: false };
    case DRAW_FINGER:
      return { ...state, fingerLoading: false };
    case ENTER_PLAYMODE:
      return {
        ...state,
        onAir: false,
        playMode: true,
        gameLoading: true,
        pianoLoading: true,
        canvasSize: undefined
      };
    case EXIT_PLAYMODE:
      return {
        ...state,
        onAir: false,
        playMode: false,
        gameLoading: true,
        pianoLoading: true,
        fingerLoading: true,
        canvasSize: undefined
      };
    case START_PLAYER:
      return { ...state, onAir: true };
    case STOP_PLAYER:
      return { ...state, onAir: false };
    case RESIZE_PLAYER:
      return { ...state, onAir: false, pianoLoading: true, gameLoading: true };
    case RESET_PLAYER:
      return { ...state, loading: true, onAir: false };
    default:
      return state;
  }
};
